<template>
  <div class="page">
    <div class="cont">
      <div class="wrap">
        <div class="item">
          <div class="label">个人头像</div>
          <div class="item-cont align-center">
            <img src="../assets/home/user_head@2x.png" alt="" class="head" />
          </div>
        </div>
        <div class="item">
          <div class="label">修改昵称</div>
          <div
            class="item-cont align-center"
            @click="$router.push('usersetname')"
          >
            <div class="name">{{ userInfo.nick_name }}</div>
            <div class="arrow-group align-center">
              <img
                src="../assets/home/arr_right@2x.png"
                alt=""
                class="arrow-right"
              />
            </div>
          </div>
        </div>
        <div class="item">
          <div class="label">手机号</div>
          <div class="item-cont align-center">
            <div class="name">{{ mobile }}</div>
          </div>
        </div>
        <div class="item noborder">
          <div class="label">活动账号（资金账号）</div>
          <div class="item-cont align-center">
            <div class="name">{{ fundAccount }}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      userInfo: JSON.parse(localStorage.getItem('userInfo')).data,
      mobile: JSON.parse(localStorage.getItem('userInfo')).data.mobile.toString().replace(/^(.{3}).*(.{4})$/, '$1****$2'),
      fundAccount: JSON.parse(localStorage.getItem('userInfo')).data.fund_account.toString().replace(/^(.{2}).*(.{4})$/, '$1****$2')
      // mobile: this.$store.getters.userInfo.mobile.toString().replace(/^(.{3}).*(.{4})$/, '$1****$2'),
      // fundAccount: this.$store.getters.userInfo.fund_account.toString().replace(/^(.{2}).*(.{4})$/, '$1****$2')
    }
  },
  components: {
  },
  created () {
  },
  methods: {

  }
}
</script>

<style lang="less" scoped>
@import url("../assets/css/mixin.less");
.page {
  width: 100%;
  height: 100%;
  background-color: #ef5d56;
  .bg-image("../assets/home/user_bg","png");
  padding: 32px;
  .cont {
    width: 100%;
    height: auto;
    min-height: 200px;
    background: #fff;
    border-radius: 16px;
    .wrap {
      width: 100%;
      height: 100%;
      padding: 22px 32px 0 32px;

      .item {
        width: 100%;
        height: 106px;
        line-height: 106px;
        border-bottom: 2px solid #efefef;
        .flex_func(row, space-between, center, nowrap);
        .label {
          font-size: 32px;
          font-family: PingFangSC-Regular, PingFang SC;
          color: #666666;
        }
        .item-cont {
          height: 100%;
          .head {
            width: 80px;
            height: 80px;
          }
          .name {
            font-size: 32px;
            font-family: PingFangSC-Regular, PingFang SC;
            color: #333333;
          }
          .arrow-group {
            height: 100%;
            margin-left: 10px;
            .arrow-right {
              width: 16px;
              height: 26px;
            }
          }
        }
      }
      .noborder {
        border: none;
      }
    }
  }
}
</style>



